<template>
    <img :src="img" alt="" v-if="img" >
    <div class="bg-dark"></div>
    <div class="indecision-container">
        <input
            type="text"
            name=""
            id=""
            placeholder="Hazme una pregunta..."
            v-model="question"
        >
        <p>Recuerda terminar con un signo de interrogación (?) tu pregunta...</p>
        <div v-if="isValidadQuestion" >
            <h2>{{ question }}</h2>
            <h1>{{ answer }}</h1>
        </div>
    </div>
</template>

<script>

export default {

    data(){
        return {
            question: null,
            answer: null,
            img: null,
            isValidadQuestion: false
        }
    },

    methods: {
        async getAnswer(){
            this.answer = 'Pensando...';

            const { answer , image } = await fetch('https://yesno.wtf/api').then( resp => resp.json() );
            this.answer = ( answer.toLowerCase() === 'yes') ? 'Si !' : 'No !' ;
            this.img = image;
        }
    },

    watch: {
        question( value , oldValue ){
            this.isValidadQuestion = false;
            if( ! value.includes('?') ) return;
            this.isValidadQuestion = true;
            this.getAnswer();
        }
    }
}

</script>

